:root {
  --main-color: rgba(255, 251, 0, 0.9);
  --second-color: rgba(34, 34, 34, 0.9);
  --second-color-active: rgb(46, 45, 45);
  --valid-color: rgb(23, 224, 23);
  --invalid-color: rgb(224, 103, 23);
  --valid-color-active: rgb(49, 194, 49);
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #233553;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #97ca3f;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
