.emply-container {
   text-align: center;
}
.emply-container img {
   width: 30%;
   height: auto;
}
.emply-container figcaption {
   font-weight: bold;
   font-size: 1.2rem;
}


.bubble {
   position: absolute;
   width: 150px;
   height: 150px;
   border-radius: 50%;
   box-shadow: inset 0 0 25px rgba (255, 255, 255, 0.25);
   left: 0;
   right: 0;
   top: 0;
   bottom: -150px;
   margin: auto;
   animation: animate_4010 8s ease-in-out infinite;
}
 
.bubble:nth-child(2) {
   position: relative;
   zoom: 0.45;
   left: -100px;
   top: 150px;
   animation-delay: -4s;
 }
 
 .bubble:nth-child(3) {
   position: relative;
   zoom: 0.45;
   right: -50px;
   top: 100px;
   animation-delay: -6s;
 }
 
 @keyframes animate_4010 {
   0%,100% {
     transform: translateY(-20px);
   }
 
   50% {
     transform: translateY(20px);
   }
 }
 
 .bubble::before {
   content: '';
   position: absolute;
   top: 50px;
   left: 45px;
   width: 20px;
   height: 20px;
   border-radius: 50%;
   background: #fff;
   z-index: 10;
   filter: blur(2px);
 }
 
 .bubble::after {
   content: '';
   position: absolute;
   top: 80px;
   left: 80px;
   width: 15px;
   height: 15px;
   border-radius: 50%;
   background: #fff;
   z-index: 10;
   filter: blur(2px);
 }
 
 .bubble span {
   position: absolute;
   border-radius: 50%;
 }
 
 .bubble span:nth-child(1) {
   inset: 10px;
   border-left: 15px solid #0fb4ff;
   filter: blur(8px);
 }
 
 .bubble span:nth-child(2) {
   inset: 10px;
   border-right: 15px solid #ff4484;
   filter: blur(8px);
 }
 
 .bubble span:nth-child(3) {
   inset: 10px;
   border-top: 15px solid #ffeb3b;
   filter: blur(8px);
 }
 
 .bubble span:nth-child(4) {
   inset: 30px;
   border-left: 15px solid #ff4484;
   filter: blur(12px);
 }
 
 .bubble span:nth-child(5) {
   inset: 10px;
   border-bottom: 10px solid #fff;
   filter: blur(8px);
   transform: rotate(330deg);
 }